.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: black;
  padding: 0.5rem;
}

.footer-link {
  color: #ccc;
  text-decoration: none;
  margin: 0.2rem 0;
  height: 24px;
}

.footer-link > img {
  height: 100%;
}

@media (min-width: 40rem) {
  .footer {
    flex-direction: row;
    justify-content: center;
  }

  .footer-link {
    height: 32px;
    margin: 0 1.5rem;
  }
}
