.navigation {
  background: black;
  padding: 1.2rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo-image {
  height: 32px;
}

.logo-image > img {
  height: 100%;
}

.navigation-sub {
  display: none;
}

.item { 
  text-decoration: none;
  color: white;
  margin: 0 1.2rem;
  transition: 0.3s;
}

.item:hover,
.active {
  color: #b82601;
}

.icon {
  font-size: 1.5rem;
  color: white;
  position: fixed;
  top: 3%;
  right: 3%;
}

.icon:hover {
  cursor: pointer;
}

.icon.open {
  color: #b82601;
}

.dropdown .mobile-nav__sub {
  display: none;
}

.dropdown .mobile-nav__sub.open {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  right: 0;
  top: 100%;
  background: white;
  width: 100%;
  height: 100vh;
  z-index: 100;
}

.dropdown .mobile-nav__sub .mobile-nav__item {
  text-decoration: none;
  color: black;
  width: 100%;
  text-align: center;
  font-family: inherit;
  padding: 2rem;
  font-weight: bold;
}

.dropdown .mobile-nav__sub .mobile-nav__item:hover {
  background: black;
  color: white;
}

@media (min-width: 40rem) {
  .navigation-sub {
    display: flex;
  }

  .dropdown {
    display: none;
  }

  .icon {
    display: none;
  }
}
