.projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
}

.projects-list {
  height: 70%;
  width: 70%;
  margin: 1rem auto;
  padding: 0.5rem 0;
}

@media (min-width: 40rem) {
  .projects {
    padding-top: 7rem;
  }

  .projects-list {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
}
